<script>
import { defineComponent, h, ref } from 'vue'
import { mixinColor, mixinView } from '../../mixins'
import { addMixinPrefix } from '../../util/helpers'

export default defineComponent({
  name: 'Card',
  props: {
    hideFooter: {
      type: Boolean,
      default: false
    },
    error:{
      type: Boolean,
      default: false
    }
  },
  mixins: [
    ...addMixinPrefix([
      mixinColor,
      mixinView('outlined')
    ], 'card')
  ],
  setup(props, { slots }) {
    const views = ref(['filled', 'smooth', 'outlined'])
    const genContent = () => {
      return h('div', {
        class: 'card__content'
      }, {
        default: () => !!slots.default ? slots.default() : ''
      })
    }
    const genContentHeader = () => {
      return h('div', {
        class: 'card__header'
      }, slots.header())
    }
    const genContentFooter = () => {
      return h('div', {
        class: 'card__footer'
      }, slots.footer())
    }

    return {
      views,
      genContent,
      genContentHeader,
      genContentFooter
    }
  },
  render() {
    const children = [
      this.$slots.header && this.genContentHeader(),
      this.genContent(),
      (this.$slots.footer && !this.$props.hideFooter) && this.genContentFooter()
    ]
    const listClass = [
      'card',
      this.$slots.header && '--with-header', this.$slots.footer && '--with-footer',
      this.classView,
      this.classColor,
      this.$props.error ? '--error' : ' ' 
    ]

    return h('div', {
      class: listClass
    }, children)
  }
})
</script>